import React, { useState } from 'react'
import { SaveOutlined, MinusCircleOutlined, PlusOutlined, CloseCircleOutlined } from '@ant-design/icons';
import { Form, Card, Col, Row, Select, Button, DatePicker, Input, InputNumber, message } from 'antd';
import NumberFormat from 'react-number-format';
import moment from 'moment';
import { useHistory } from "react-router-dom";
import { DataService } from "services/garage.service";
import { DataService as ToolsService } from "services/garageTools.service";
// import { DataService as RecordsService } from "services/garageRecords.service";
// import Invoice from '../invoice';

const { TextArea } = Input;
const dateFormat = 'DD/MM/YYYY';
const { Option } = Select;
let offGrid = {
	visible: false,
	receiptTotal: 0,
	receiptId: '',
	salesInfo: {},
	shopName: {},
	orderDetails: [
		{ toolName: '', quantity: '0', price: '0', total:'0' }
	  ]
};
const Sales = ({systemData}) => { 	
	const [form] = Form.useForm();
	const [dates, setDates] = useState(new Date());
	const [garageName, setGarageName] = useState('');
	const [description, setDescription] = useState('');
	const [truckId, setTruckId] = useState();
	const [grandTotal, setGrandTotal] = useState(0);
	const [rate, setRate] = useState(1);
	const [totalAmount, setTotalAmount] = useState(0);

	const [visible, setVisible] = useState(offGrid.visible);
	const [selectedCurrency, setSelectedCurrency] = useState();
	const [buttonLoading, setButtonLoading] = useState(false);
	const [inputFields, setInputFields] = useState([
	  { toolName: '',  quantity: '0', price: '0', total: '0' }
	]);
	const { userData, trucks, trucksLoading,  allCurrencyLoading, allCurrency, currencies, currenciesLoading, trips, tripsLoading,  } = systemData; 
	let history = useHistory();	
	const RedirectBack = () => {
		history.push(`/app/apps/garage/list`)
	}
	const handleSubmit = async () => {
		setButtonLoading(true)
		try {
			const row = await form.validateFields();
			let values = [...inputFields];
			let recId = dates;
			offGrid.receiptId = recId;
			let recordId = +(recId.getTime());
			let records = {
				date: recordId,
				garageName,
				userId : userData.userId,
				backupUser:userData.displayName,
			}
			
			let garageId = +(recId.getTime());
			let data = {
				date: dates.getTime(),
				truckId,
				tools: values.length,
				amount: grandTotal,
				totalAmount,
				currency: selectedCurrency,
				exchangeRate: rate,
				description,
				garageName,
				userId : userData.userId,				
				backupUser:userData.displayName,
			}

			if(!tripsLoading){
				let trps = trips.docs.reverse().filter(x=> +x.data().truckId == +truckId && x.data().status == 'En Route');
				if(trps.length > 0 && +trps[0].data().orderId) {
					data.orderId = trps[0].data().orderId;
					data.tripId = trps[0].data()._id;
				}
				
			}

			values.map(function(record){
			let purchases = {
				date:+(dates.getTime()),
				toolName : record.toolName,
				price : record.price,
				quantity : record.quantity,
				total : record.total,
				garageId,
				backupUser:userData.displayName,
			};
			return ToolsService.create(purchases);
			});
			setTimeout(() => {
				offGrid = {
					visible: false,
					receiptTotal: 0,
					receiptId: '',
					salesInfo: {},
					shopName: {},
					orderDetails: [
						{ toolName: '',  quantity: '0', price: '0', total: '0' }
					  ]
				};
			  }, 3000);
			DataService.create(data,garageId);

			

			offGrid.receiptTotal = grandTotal;
			offGrid.orderDetails = values;
			offGrid.salesInfo = data;
			
			setInputFields([{ toolName: '',  quantity: '0', price: '0', total: '0' }]);
			setGrandTotal(0);
			setRate(0);	
			setTotalAmount(0);	
			setGarageName('');
			setButtonLoading(false)
			RedirectBack();
			//offGrid.visible = true;
			//setVisible(offGrid.visible);
			message.success("Garage activities have been recorded!")
		} catch (errInfo) {setButtonLoading(false)}	
	};
	
	const handleAddFields = () => {
		const values = [...inputFields];
		values.push({ toolName: '',  quantity: '0', price: '0', total: '0' });
		setInputFields(values);
	};
	
	const handleDateChange = date => {
		setDates(date ? new Date(date.format('YYYY-MM-DD HH:mm:ss')) : new Date());
		setInputFields([
			{ toolName: '',  quantity: '0', price: '0', total: '0' }
		  ]);
		setGrandTotal(0);
		setRate(0);	
		setTotalAmount(0);	
		form.setFieldsValue({'PaidAmount': null, paymentMethod: null, debtDate:null});
	};
			
	const handleNameChange = event => {
		setGarageName(event.target.value);
	};

	const handleDescr = event => {
		setDescription(event.target.value);
	}

	const handleToolName = (index, event) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		values[index].toolName = event.target.value;
		setInputFields(values);
	}

	const handleQuantityChange = (index, value) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		let newValue = value === '' ? '' : parseFloat(value);
		
		if(newValue || newValue === ''){
			let newTotal = grandTotal - values[index].total;
			values[index].quantity = newValue;
			values[index].total = (newValue) ? values[index].price * newValue : 0 ;  
			setGrandTotal(newTotal + values[index].total);  
			let total = rate*(newTotal + values[index].total);
			setTotalAmount(total);	
		}
		setInputFields(values);
	}
	
	const handlePriceChange = (index, newValue) => {
		const values = [...inputFields];
		if(!values[index]) return;
		let newTotal = grandTotal - values[index].total;
		values[index].price = newValue;
		values[index].total = (newValue && values[index].quantity) ? values[index].quantity * newValue : 0;  
		setGrandTotal(newTotal + values[index].total);  
		let total = rate*(newTotal + values[index].total);
		setTotalAmount(total);
		setInputFields(values);
	};
	
	
	const handleRemoveFields = index => {
		const values = [...inputFields];
		if(!values[index]) return;
		let newTotal = grandTotal - values[index].total;
		let total = rate*newTotal;
		setTotalAmount(total);
		setGrandTotal(newTotal);
		values.splice(index, 1);
		setInputFields(values);
	};  
	const disabledDate = (current) => {
		return current && current > moment().endOf("day");
	}  

	const handleRateChange = (value) => {
		let total = value*grandTotal;
		setRate(value);
		setTotalAmount(total);
	}

	const handleCurrencyChange = (currency) => {
		setSelectedCurrency(currency)
	}

	const handleTruckSelect = (truckId) => {
		setTruckId(truckId);
	}

	return (
		<>
		<div className={(visible) ? 'container d-none' : 'container'}>
			<Card>
				<Form
					//layout="horizontal"
					layout="vertical" hideRequiredMark form={form}
				>
					<div className="d-md-flex justify-content-md-between">
						<div className="">
							<h2 className="mb-3 font-weight-semibold">Truck Garage Form</h2>
							<Form.Item
									label="Select truck"
									name="truckId"
									rules={[{ required: true,message: `Truck is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
									className="w-100"
									placeholder="-Select-"
									onChange={handleTruckSelect}
									>
										{trucksLoading ? null : trucks.docs.map((truck, index)=><Option value={truck.data()._id} key={index}>{truck.data().plateNumber} - Driver: {truck.data().driverName}</Option>)}
									</Select>
								</Form.Item>
							<Form.Item style={{ margin: 0, padding: 0 }}>
								<Form.Item
									name="garageName"
									label="Name"
									rules={[{ required: true, message:"Enter name" }]}
									style={{ display: 'inline-block' }}
									className="w-100"
								>
									<Input placeholder="Enter mechanic name or garage" value={garageName} onChange={handleNameChange}/>
								</Form.Item>
								<Form.Item
									name="description"
									label="Garage activities description "
									rules={[{ required: true, message:"Enter activities description" }]}
									style={{ display: 'inline-block', width: 'calc(40% - 5px)' }}
									className="w-100" 
								>
									<Input placeholder="Enter description" onChange={handleDescr}/>
								</Form.Item>
								
								
								
							</Form.Item>
						</div>
					<div className="mt-3 text-right">
						<Form.Item name="dates">
							<DatePicker defaultValue={moment(new Date(), dateFormat)} format={dateFormat} disabledDate={disabledDate} onChange={handleDateChange}/>
						</Form.Item>
						
					</div>
				</div>
				<div className="mt-5 mb-5">
				{inputFields.map((inputField, index) => (
				<Row gutter={16} className="w-100" key={`${inputField}~${index}`}>
					<Col span={6}>
						<Form.Item name={`toolName${index}`} label="Item" rules={[
							{
							required: true,
							message: `Item is required.`,
							},
						]}>
							<Input
								className="w-100"
								value={inputField.toolName}
								onChange={value => handleToolName(index, value)}
							/>
						</Form.Item>
					</Col>
					<Col span={6}>
						<Form.Item name={`quantity${index}`} label="Quantity" rules={[
							{
							required: true,
							message: `This field is required.`,
							},
						]}>
							<InputNumber className="w-100" min={0} 
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
										value={inputField.quantity}
										onChange={value => handleQuantityChange(index, value)}/>
						</Form.Item>
					</Col>
					<Col span={6}>
						<Form.Item name={`price${index}`} label="Unit Price" rules={[
							{
							required: true,
							message: `This field is required.`,
							},
						]}>
							<InputNumber className="w-100" min={1} 
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
										value={inputField.price}
										onChange={value => handlePriceChange(index, value)}
										/>
						</Form.Item>
					</Col>
					<Col span={3}>
						<Form.Item label="Total">
							<InputNumber className="w-100" min={0} value={inputField.total} formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')} disabled/>
						</Form.Item>
					</Col>
					<Col span={3}>
						{inputFields.length > 1 ? (
							<Form.Item label=" ">
								<MinusCircleOutlined
								className="ml-2"
								style={{fontSize: '24px'}}
								onClick={() => handleRemoveFields(index)}
								/>
							</Form.Item>
						) : null}	
					</Col>
				</Row>
				))}		
					<Form.Item>
						<Button
						type="dashed"
						onClick={() => handleAddFields()}
						>
						<PlusOutlined /> Add field
						</Button>
					</Form.Item>  
					<div className="d-flex justify-content-end mt-4">
						<div className="text-right ">
							<div className="border-bottom text-left">
								<Form.Item style={{ margin: 0, padding: 0 }}>
									<Form.Item 
										name="currency" 
										label={`Select Currency \n Used`} 
										rules={[{ required: true,message: `Currency is required` }]}
										style={{ display: 'inline-block', width: 'calc(50% - 5px)', marginRight: 8 }}
									>
										<Select
											placeholder="- Select Currency Used -"
											onChange={handleCurrencyChange}
										>
											{!currenciesLoading ? currencies.docs.map((currency,index)=><Option value={currency.data()._id} key={index} className='w-100 pe-5'>{currency.data().name}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Option>) : null }
										</Select>
									</Form.Item>
									<Form.Item 
										name="exchangeRate" 
										label="Exchange Rate vs 1RWF" 
										rules={[{ required: true,message: `Exchange rate is required` }]}
										style={{ display: 'inline-block', width: 'calc(50% - 5px)' }}
									>
										<InputNumber
											className="w-100"
											min={0}
											onChange={handleRateChange}
											parser={value => value.replace(/\$\s?|(,*)/g, '')}
											formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
										/>
									</Form.Item>
								</Form.Item>
							</div>
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Grand Total: </span>
								<NumberFormat 
									displayType={'text'} 
									value={grandTotal} 
									suffix={(allCurrencyLoading || !selectedCurrency) ? '' : allCurrency[selectedCurrency]?.name}
									thousandSeparator={true}
								/>
							</h2>
							{totalAmount ? <h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Total in RWF: </span>
								<NumberFormat 
									displayType={'text'} 
									value={totalAmount} 
									suffix={'RWF'}
									thousandSeparator={true}
								/>
							</h2> : null }
						</div>
					</div>
					
				</div>
				<hr className="d-print-none"/>
				<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<CloseCircleOutlined /> Cancel
					</Button>
					<Button type="primary" loading={buttonLoading} disabled={grandTotal===0 || buttonLoading} onClick={handleSubmit}>
						<SaveOutlined  />
						Save
					</Button>
				</div>
				</Form>
			</Card>
		</div>
		</>
	);	
}

export default Sales
