import { Timeline } from 'antd';
import NumberFormat from 'react-number-format';

const findMonths = (settings,shares) => {
  let data = settings['startDate'].value;
  let monthly = settings['monthlyPrice'].value;
  let lMonth = new Date(data).getMonth();
  let lYear = new Date(data).getFullYear();
  let dates = new Date().getDate();
  let month = new Date().getMonth();
  let year = new Date().getFullYear(); 
  let counter = 0;
  for(let j = lYear; j <= year; j++){
    let limiter = (lYear === year || j === year) ? month : 11;
    let starter = (lYear === year || j === lYear) ? lMonth : 0;
    for(let i = starter; i<= limiter; i++){
      counter += (j === year && i === month && dates < 5) ? 0 : 1;
    }
  }
  return (
    <Timeline className="text-left pt-2">
      <Timeline.Item>
        <NumberFormat displayType={'text'} 
                      value={counter*shares*monthly} 
                      suffix={'Rwf monthly savings'} 
                      thousandSeparator={true}/>
      </Timeline.Item>
    </Timeline>
  );
}

const getAmount = (settings,date,dateJoined,current) => {
  let time = ''+new Date(date).setHours(0,0,0,0);
  current = parseInt(current);
  dateJoined = parseInt(dateJoined);
  if(current < dateJoined){
    return {amount: 0, current}
  }
  let amount = (settings[time] && settings[time].value) 
                ? settings[time].value : settings[current].value;
  current = (settings[time] && settings[time].value) ? time : current;
  return {amount, current};
}

const unpaidMonths = (settings,dateJoined) => {
  let data = settings['startDate'].value;
  let lMonth = new Date(data).getMonth();
  let lYear = new Date(data).getFullYear();
  let month = new Date().getMonth();
  let year = new Date().getFullYear(); 
  let counter = 0;
  let current = data;
  let historic = [];
  for(let j = lYear; j <= year; j++){
    let limiter = (lYear === year || j === year) ? month : 11;
    let starter = (lYear === year || j === lYear) ? lMonth : 0;
    for(let i = starter; i<= limiter; i++){
      let date = j+'-'+parseInt(i+1)+'-01';
      let info = getAmount(settings,date,dateJoined,current);
      current = info.current;
      if(info.amount > 0){
        historic.push({month:(parseInt(i+1)<10?'0':'')+parseInt(i+1)+'-'+j, amount: info.amount})
      }
      counter += info.amount;
    }
  }

  return (
    <>
    {counter ? (<Timeline className="text-left pt-3">
      {historic.map((data, index) =>(
        <Timeline.Item key={index}>
        <NumberFormat displayType={'text'} 
                      value={data.amount} 
                      suffix={' Rwf'} 
                      prefix={data.month+': '}
                      thousandSeparator={true}/>
      </Timeline.Item>
      ))}
    </Timeline>) : (<p className="w-100 pt-3">No Pending Payments</p>)}
    </>
  );
}

const unpaidList = (settings,dateJoined) => {
  let data = settings['startDate'].value;
  let lMonth = new Date(data).getMonth();
  let lYear = new Date(data).getFullYear();
  let month = new Date().getMonth();
  let year = new Date().getFullYear(); 
  let counter = 0;
  let current = data;
  let historic = [];
  for(let j = lYear; j <= year; j++){
    let limiter = (lYear === year || j === year) ? month : 11;
    let starter = (lYear === year || j === lYear) ? lMonth : 0;
    for(let i = starter; i<= limiter; i++){
      let date = j+'-'+parseInt(i+1)+'-01';
      let info = getAmount(settings,date,dateJoined,current);
      current = info.current;
      if(info.amount > 0){
        historic.push({month:j+'-'+(parseInt(i+1)<10?'0':'')+parseInt(i+1)+'-01', amount: info.amount})
      }
      counter += info.amount;
    }
  }
  return {historic,counter};
}

const unpaidBalance = (settings,dateJoined) => {
  let data = settings['startDate'].value;
  let lMonth = new Date(data).getMonth();
  let lYear = new Date(data).getFullYear();
  let month = new Date().getMonth();
  let year = new Date().getFullYear(); 
  let counter = 0;
  let current = data;
  for(let j = lYear; j <= year; j++){
    let limiter = (lYear === year || j === year) ? month : 11;
    let starter = (lYear === year || j === lYear) ? lMonth : 0;
    for(let i = starter; i<= limiter; i++){
      let date = j+'-'+parseInt(i+1)+'-01';
      let info = getAmount(settings,date,dateJoined,current);
      current = info.current;
      counter += info.amount;
    }
  }

  return (
        <NumberFormat displayType={'text'} 
                      value={counter} 
                      suffix={' Rwf'} 
                      thousandSeparator={true}/>
     
  );
}

const nextMonth = () => {
  let now = new Date();
  let current = '';
  if (now.getMonth() === 11) {
      current = new Date(now.getFullYear() + 1, 0, 1).setHours(0,0,0,0);
  } else {
      current = new Date(now.getFullYear(), now.getMonth() + 1, 1).setHours(0,0,0,0);
  }
  return current;
}

const thisMonth = () => {
  let now = new Date();
  let current = new Date(now.getFullYear(), now.getMonth(), 1).setHours(0,0,0,0);
  return current;
}

const unpaidPenalty = (member) => {
  return member
}

const paidPenalty = (member) => {
  return member
}

const getNormalDate = (date) => {
  const todayDate = new Date(+date);
	let tYear = todayDate.getFullYear();
	let tMonth = todayDate.getMonth()+1;
	let tDate = todayDate.getDate();
	tMonth = tMonth < 10 ? '0'+tMonth : tMonth;
	tDate = tDate < 10 ? '0'+tDate : tDate;
  return tDate+'-'+tMonth+'-'+tYear;
}

export const Balance = {
  findMonths,
  unpaidPenalty,
  unpaidMonths,
  unpaidBalance,
  paidPenalty,
  nextMonth,
  unpaidList,
  thisMonth,
  getNormalDate
};