import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom';
import GarageList from './list'
import Add from './add'

const Employees = ({match, systemData}) => {
	return (
		<Switch>
			<Redirect exact from={`${match}`} to={`${match}/list`} />
			<Route path={`${match}/add`} render={ () => (<Add systemData={systemData}/>) } />
			{/* <Route path={`${match}/edit/:id`} component={EditEmployee} />			 */}
			<Route path={`${match}/list`} render={ () => (<GarageList systemData={systemData}/>) } />
		</Switch>
	)
}

export default Employees
